.tcp__tintucchitiet {
    display: flex;
    flex-direction: column;
}

.tcp__tintucchitiet-pads {
    display: flex;
    flex-direction: row;
    margin: auto;
    z-index: 0;
}

.tcp__tintucchitiet-box {
    display: flex;
    align-items: center;
    min-width: 100px;
    max-width: 400px;
    min-height: 20px;
    max-height: 300px;
    padding: 1rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

.tcp__tintucchitiet h1 {
    font-size: 20px;
}

@media screen and (max-width: 1400px) {
    .tcp__tintucchitiet-pads {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 1200px) {
    .tcp__tintucchitiet-header h1 {
        margin: auto;
    }
}

@media screen and (max-width: 700px) {
    .tcp__tintucchitiet-header h1 {
        font-size: 35px;
        line-height: 60px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__tintucchitiet-header h1 {
        font-size: 28px;
        line-height: 50px;
    }
}