.tcp__tintuc {
    display: flex;
    flex-direction: column;
}

.tcp__tintuc-header {
    display: flex;
    flex-direction: column;
    padding-bottom: 1rem;
}

.tcp__tintuc-header h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 100px;
}

.tcp__tintuc-header-items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}

.tcp__tintuc-header-text {
    max-width: 600px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: var(--color-text);
}

.tcp__tintuc-header-link {
    display: flex;
    justify-content: flex-end;
    min-width: 80px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    
    color: var(--color-text);
    cursor: pointer;
}

.tcp__tintuc-pads {
    display: flex;
    flex-direction: row;
    margin: auto;
    z-index: 0;
}

.tcp__tintuc-btn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 30px;
    margin-top: 2rem;
    margin-left: 2rem;
    
    border: none;
    padding: 0.5rem 2rem;
    color: #fff;
    cursor: pointer;

}

@media screen and (max-width: 1400px) {
    .tcp__tintuc-pads {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 1200px) {
    .tcp__tintuc-header h1 {
        margin: auto;
    }
}

@media screen and (max-width: 700px) {
    .tcp__tintuc-header h1 {
        font-size: 35px;
        line-height: 60px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__tintuc-header h1 {
        font-size: 28px;
        line-height: 50px;
    }
}