.tcp__ttskpostcard {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    max-width: 1600px;
    padding-top: 5rem;
    padding-bottom: 5rem;
    margin-top: 5rem;
    margin-bottom: 5rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

.tcp__ttskpostcard-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
}

.tcp__ttskpostcard-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
}

.tcp__ttskpostcard-time {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tcp__ttskpostcard-detail {
    display: flex;
    flex-direction: row;
}

.tcp__ttskpostcard-content {
    margin-top: 2rem;
    min-width: 200px;
    max-width: 1400px;
    align-items: center;
    font-size: 18px;
    line-height: 30px;
    margin-top: 2rem;
}

.tcp__ttskpostcard-content img {
    min-width: 200px;
    max-width: 1400px;
    min-height: 200px;
    max-height: 1000px;
    margin: 2rem auto;
    
}

