.modalBackground {
    width: 600px;
    background-color: transparent;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContainer {
    width: 300px;
    height: 650px;
    border-radius: 12px;
    background-color: var(--color-footer);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
  }
  
  .modalContainer .title {
    display: inline-block;
    text-align: center;
    margin-top: 1rem;
  }
  
  .title {
    display: inline-block;
    color:burlywood;
    text-align: center;
    margin-bottom: 1rem;
  }

  .modalContainer .body {
    flex: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    text-align: center;
  }
  
  .modalContainer .footer {
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #okBtn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 16px;

    border: none;
    width: 100px;
    padding: 0.5rem 0.5rem;
    margin: 0.5rem ;

    color: #fff;
    cursor: pointer;
  }
  
  #cancelBtn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #ff0d00 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(255, 4, 0, 0.64) 319.41deg, #ff0400 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 16px;

    border: none;
    width: 100px;
    padding: 0.5rem 0.5rem;
    margin: 0.5rem ;

    color: #fff;
    cursor: pointer;
  }