body {
    background: var(--color-bg);
}

.tcp__userinfo {
    display: flex;
    flex-direction: column;
}

.tcp__userinfo-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-self: center;
    font-size: 14px;
}

.tcp__userinfo-bar-icon{
    padding: 0;
    font-size: 25px;
}

.tcp__userinfo-box {
    top:10px;
    right: -20px;
    position: fixed;
    display: flex;
    align-items: center;
    flex-direction: column;
    min-width: 100px;
    max-width: 400px;
    min-height: 20px;
    max-height: 200px;
    padding: 1rem;
    margin-left: 2rem;
    margin-right: 3rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 0.1px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 10px;
    
    z-index: 5;
}

.tcp__userinfo-duan {
    margin: auto;
    min-width: 300px;
    max-width: 1900px;    
    padding: 2rem;
}

.tcp__userinfo-tables {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
}

.tcp__userinfo-duan h1 {
    margin: auto;
    font-family: var(--font-family);
    font-size: 20px;    
}

.tcp__userinfo-taikhoan {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-between;
}

.tcp_userinfo-vi {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-between;
}

.tcp__userinfo-taisan {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-between;
}

.tcp__userinfo-revenue {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-between;
}

.tcp__vi-naprut {
    display: flex;
    flex-direction: row;
}

.tcp__vi-naprut-box {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 5rem;
    margin-left: 10rem;
    margin-right: 10rem;
    width: 400px;
}

.tcp__userinfo-btn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;
    align-self: center;

    font-family: var(font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    margin-top: 2rem;
    margin-left: 2rem;
    width: 150px;
    border: none;
    color: #fff;
    cursor: pointer;

}

.tcp__userinfo-styled-table {
    border-collapse: collapse;
    margin: auto;
    font-size: 14px;
    font-family: var(--font-family);
    color: var(--color-text-white);
    box-shadow: 0.1px 0.1px 1px rgba(0, 0, 0, 0.15);
}

.tcp__userinfo-styled-table thread tr {
    background-color: var(--gradient-pad);
    color: var(--color-text-white);
    text-align: center;
}

.tcp__userinfo-styled-table th,
.tcp__userinfo-styled-table td {
    padding: 5px 5px;
    width: 150px;
    height: 20px;
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__userinfo-styled-table tbody tr {
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__userinfo-styled-table tbody tr:nth-of-type(even) {
    background-color: var(--color-bg-lightgreen);
}

.tcp__userinfo-styled-table tbody tr:last-of-type {
    border-bottom: 0.1px solid var(--color-text-black);
}

.tcp__userinfo-href a {
    color: rgb(113, 0, 0);
}

input[type="text"],
input[type="number"],
button,
textarea {
  font-family: var(--font-family);
  font-size: 14px;
  padding: 0.25rem;
  border-radius: 10px;
  color: rgb(0, 31, 3);
  background-color: rgb(255, 255, 255);
}

@media screen and (max-width: 1100px) {
    .tcp__vi-naprut-box {
        padding-left: 1rem;
        padding-right: 1rem;
        margin-top: 2rem;
        margin-left: 2rem;
        margin-right: 2rem;
        width: 400px;
    }
}

@media screen and (max-width: 850px) {
    .styled-table {
        display: none;
    }
    .tcp__vi-naprut {
        display: flex;
        flex-direction: column;
    }
    .tcp__vi-naprut-box {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        margin-top: 1rem;
        margin-left: 0.5rem;
        margin-right: 0.5rem;
        width: 400px;
    }
}
