.tcp__userproperty-styled-table {
    border-collapse: collapse;
    margin: auto;
    font-size: 14px;
    font-family: var(--font-family);
    color: var(--color-text-white);
    box-shadow: 0.1px 0.1px 1px rgba(0, 0, 0, 0.15);
}

.tcp__userproperty-styled-table thread tr {
    background-color: var(--gradient-pad);
    color: var(--color-text-white);
    text-align: center;
}

.tcp__userproperty-styled-table th,
.tcp__userproperty-styled-table td {
    padding: 5px 5px;
    width: 150px;
    height: 20px;
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__userproperty-styled-table tbody tr {
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__userproperty-styled-table tbody tr:nth-of-type(even) {
    background-color: var(--color-bg-lightgreen);
}

.tcp__userproperty-styled-table tbody tr:last-of-type {
    border-bottom: 0.1px solid var(--color-text-black);
}

input[type="text"],
input[type="number"],
button,
textarea {
  font-family: var(--font-family);
  font-size: 14px;
  padding: 0.25rem;
  border-radius: 10px;
  color: rgb(0, 31, 3);
  background-color: rgb(255, 255, 255);
}