.tcp__useraccount {
    display: flex;
    flex-direction: row;
    padding-top: 5rem;
}

.tcp__useraccount h1 {
    font-family: var(--font-family);
    font-size: 16px;
}

.tcp__useraccount-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 1600px;
    padding: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 10px;
}
.tcp__useraccount-content p {
    font-size: 25px;
}

.tcp__useraccount-info-show {
    display: flex;
    flex-direction: row;
    padding: 0.5rem;
}

.tcp__useraccount-box {
    display: flex;
    align-items: flex-start;
    min-width: 100px;
    max-width: 400px;
    min-height: 20px;
    max-height: 60px;
    padding: 0.5rem;
    margin-right: 3rem;
    
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 10px;
}

@media screen and (max-width: 1600px) {
    .tcp__useraccount-header {
        margin: auto;
    }
    .tcp__useraccount-content {
        flex-direction: column;
        width: 400px;
    }

    .tcp__useraccount-content-feature {
        margin: 0;
    }
    .tcp__useraccount-info-show {
        display: flex;
        flex-direction: column;
        padding: 1rem;
    }
}

@media screen and (max-width: 1200px) {
    .tcp__useraccount-header {
        margin: auto;
    }
    .tcp__useraccount-content {
        width: 300px;
    }

    .tcp__useraccount-content-feature {
        margin: 0;
    }
    .tcp__useraccount-info-show {
        display: flex;
        flex-direction: column;
        padding: 1rem;
    }
}

@media screen and (max-width: 700px) {
    .tcp__useraccount-header h1 {
        font-size: 35px;
        line-height: 60px;
    }
    .tcp__useraccount {
        display: flex;
        flex-direction: column;
        padding: 1rem;
    }
}

@media screen and (max-width: 410px) {
    .tcp__useraccount-header h1 {
        font-size: 28px;
        line-height: 50px;
    }
}