.tcp__dapostcard {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    max-width: 1600px;
    padding: 2rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

.tcp__dapostcard h1 {
    padding: 1rem;
    font-family: var(font-family);
    font-weight: 700;
    font-size: 20px;
}

.tcp__dapostcard-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
}

.tcp__dapostcard-title svg {
    width: 30px;
    margin-left: 2rem;
}

.tcp__dapostcard-time {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tcp__dapostcard-detail {
    display: flex;
    flex-direction: row;
}

.tcp__dapostcard-content {
    margin-top: 2rem;
    min-width: 200px;
    max-width: 800px;
    align-items: center;
    font-size: 16px;
    padding-right: 1ren;
}

.tcp__dapostcard-content img {
    min-width: 200px;
    max-width: 600px;
    min-height: 200px;
    max-height: 600px;
    padding: 2rem 2rem;
}

.tcp__slit-dapostcard-info-tcp__postcard-boxmua {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tcp__dapostcard-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    min-width: 200px;
    max-width: 600px;
    min-height: 50px;
    max-height: 1000px;
    padding: 2rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

.tcp__dapostcard-info-show {
    display: flex;
    align-items: flex-start;
    padding: 0.5rem;
    
}

.tcp__dapostcard-box {
    display: flex;
    align-items: center;
    min-width: 200px;
    max-width: 600px;
    min-height: 20px;
    max-height: 300px;
    padding: 1rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}
.postcard-btn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 16px;

    border: none;
    width: 150px;
    padding: 0.5rem 0.5rem;
    margin: 0.5rem ;

    color: #fff;
    cursor: pointer;
}

.tcp__postcard-boxmua {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 5rem;
    margin-left: 2rem;
    margin-right: 2rem;
    height: 300px;
    width: 200px;
}

@media screen and (max-width: 1200px) {
    .tcp__dapostcard {
        margin: 0;
    }
    .tcp__dapostcard-detail {
        margin: 0;
    }
    .tcp__dapostcard-content {
        margin: 0;
        width: 500px;
    }
    .tcp__dapostcard-content img{
        width: 400px;
        height: 400px;
    }
}

@media screen and (max-width: 1000px) {
    .tcp__dapostcard {
        background: transparent;
    }
    .tcp__dapostcard-detail {
        background: transparent;
    }
    .tcp__dapostcard-content {
        padding-right: 2rem;
        width: 350px;
        background: transparent;
    }
    .tcp__dapostcard-info {
        width: 350px;
    }
    .tcp__dapostcard-content img{
        width: 350px;
        height: 350px;
    }

}

@media screen and (max-width: 900px) {
    .tcp__dapostcard-content {
        padding-right: 2rem;
        width: 300px;
        background: transparent;
    }
    .tcp__dapostcard-info {
        width: 300px;
    }
    .tcp__dapostcard-content img{
        width: 300px;
        height: 300px;
    }

}

@media screen and (max-width: 700px) {
    .tcp__dapostcard-detail {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .tcp__dapostcard-content img{
        width: 300px;
        height: 300px;
    }

    .tcp__dapostcard-info {
        display: flex;
        flex-direction: column;
        padding-top: 2rem;
        margin: 0;
        align-items: center;
    }

    .tcp__dapostcard-info-show {
        display: flex;
        flex-direction: column;
        padding-top: 2rem;
        margin: 0 0 0 0;
        align-items: center;
    }

}
