.tcp__duyetnaprut{
    display: flex;
}

.tcp__duyetnaprut-styled-table {
    border-collapse: collapse;
    margin: auto;
    font-size: 14px;
    font-family: var(--font-family);
    color: var(--color-text-white);
    box-shadow: 0.1px 0.1px 1px rgba(0, 0, 0, 0.15);
}

.tcp__duyetnaprut-styled-table thread tr {
    background-color: var(--gradient-pad);
    color: var(--color-text-white);
    text-align: center;
}

.tcp__duyetnaprut-styled-table th,
.tcp__duyetnaprut-styled-table td {
    padding: 5px 5px;
    width: 150px;
    height: 20px;
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__duyetnaprut-styled-table tbody tr {
    border: 0.1px solid var(--color-bg-lightgreen);
}

.tcp__duyetnaprut-styled-table tbody tr:nth-of-type(even) {
    background-color: var(--color-bg-lightgreen);
}

.tcp__duyetnaprut-styled-table tbody tr:last-of-type {
    border-bottom: 0.1px solid var(--color-text-black);
}

.tcp__duyetnaprut-btn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;
    align-self: center;

    font-family: var(font-family);
    font-weight: 600;
    font-size: 14px;
    margin: 1rem;
    width: 100px;
    border: none;
    color: #fff;
    cursor: pointer;
}

.choduyet {
    color: aqua;
}

.daduyet {
    color:bisque;
}