.tcp__duanchitiet {
    display: flex;
    flex-direction: column;
}
.tcp__duanchitiet h1 {
    font-size: 20px;
}

.home-btn {
    position: sticky;

    width: 30px;
    height: 30px;
    border-radius: 50%;
    top: 50%;
    left: 98%;

    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #001988 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #2d039e 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 16px;

    border: none;
    color: #fff;
    cursor: pointer;
    z-index: 10;
}

.tcp__duanchitiet-box {
    display: flex;
    align-items: center;
    min-width: 100px;
    max-width: 400px;
    min-height: 20px;
    max-height: 300px;
    padding: 1rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 240, 66, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

@media screen and (max-width: 820px) {
    .tcp__duanchitiet-slider {
        padding: 0;
        margin: 0;
    }
}